.containerAnime {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  /* bottom: -205px; */
  width: 100%;
  height: 0px;
  z-index: 1;
}
.containerAnime div {
  position: absolute;
}
.containerAnime .me {
  left: 0;
  position: absolute;
  right: 0;
  margin: auto;
  width: 80px;
  height: 160px;
}
.containerAnime .me .head {
  transition: all 0.5s ease;
  width: 70px;
  height: 90px;
  background: #ffd3c1;
  border-radius: 100px;
  top: 10px;
  left: 5px;
}
.containerAnime .me .head .bangs {
  transition: all 0.5s ease;
  height: 50px;
  width: 94%;
  background: #111427;
  content: "";
  position: absolute;
  border-radius: 100px 100px 10px 10px;
  top: -12px;
  left: 3px;
}
.containerAnime .me .look-left.head {
  width: 70px;
  left: -10px;
}
.containerAnime .me .look-left.head .bangs {
  width: 106%;
  left: -7px;
}
.containerAnime .me .look-right.head {
  width: 70px;
  left: 15px;
}
.containerAnime .me .look-right.head .bangs {
  width: 106%;
  left: 5px;
